<template>
  <div id="summary">
    <!-- 操作区 -->
    <el-row class="text-right">
      <el-button type="primary" @click="insert">添加</el-button>
      <el-button type="danger" @click="remove">删除</el-button>
    </el-row>
    <!-- 表格区 -->
    <el-row id="plan">
      <el-table @select="rowclick" show-summary border :data="tableData">
        <el-table-column label="钩子采购计划单">
          <el-table-column type="selection" width="50"></el-table-column>
          <el-table-column
            label="序号"
            type="index"
            width="50"
          ></el-table-column>
          <el-table-column
            :label="item.label"
            :width="item.width"
            v-for="item in tableHeaderItems1"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="钩子照片" width="200">
            <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-table-column>
          <el-table-column
            :label="item.label"
            :width="item.width"
            v-for="item in tableHeaderItems2"
            :key="item.index"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row[item.prop]"></el-input>
            </template>
          </el-table-column>
        </el-table-column>
      </el-table>
      <el-col
        :span="1"
        style="
          line-height: 60px;
          border-left: 1px solid;
          border-bottom: 1px solid;
        "
        >备注：</el-col
      >
      <el-col
        :span="23"
        style="
          line-height: 60px;
          border-right: 1px solid;
          border-left: 1px solid;
          border-bottom: 1px solid;
        "
      >
        <el-input type="textarea" v-model="comment"></el-input>
      </el-col>
    </el-row>
    <el-row>
      <!-- 提交区 -->
      <el-col class="text-right">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button type="warning" @click="cancel">取消</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  InsertOrUpdateHook,
  GetRelatedHooks,
  DeleteHook,
} from "network/hook.js";
import { arraySort } from "service/dateUtil.js";

import { TableItem } from "service/Business/hook.js";
export default {
  data() {
    return {
      tableData: [
      ],
      tableHeaderItems1: [
        {
          label: "订单号",
          width: "150",
          prop: "orderNumber",
        },
        {
          label: "业务员",
          width: "150",
          prop: "salesManName",
        },
        {
          label: "客户",
          width: "150",
          prop: "customerName",
        },
        {
          label: "货号",
          width: "150",
          prop: "productId",
        },
        {
          label: "钩子名称",
          width: "150",
          prop: "hookName",
        },
      ],
      tableHeaderItems2: [
        {
          label: "数量",
          width: "150",
          prop: "number",
        },
        {
          label: "交期",
          width: "150",
          prop: "requiredTime",
        },
        {
          label: "采购方",
          width: "150",
          prop: "purchaser",
        },
        {
          label: "供应商",
          width: "150",
          prop: "supplier",
        },
        {
          label: "下单日期",
          width: "150",
          prop: "creationTime",
        },
        {
          label: "单价(元/个)",
          width: "150",
          prop: "price",
        },
        {
          label: "备注1",
          width: "150",
          prop: "comment2",
        },
      ],
      dialogImageUrl: "",
      selectData: [],
      dialogVisible: false,
      getRelatedHook: {
        guid: "",
        pageSize: 10,
        pageNumber: 1,
      },
      deleteData: [],
      isContainBlank: false,
    };
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      //判断新建还是编辑
      //新建
      if (this.$route.query.iid === undefined) {
      }
      //编辑
      else {
        this.getRelatedHook.guid = this.$route.query.iid;
        GetRelatedHooks(this.getRelatedHook).then((res) => {
          this.tableData = res.result.hookDto;
          this.tableData.forEach((item, index) => {
            item.index = index;
          });
        });
      }
    },
    //选择选框
    rowclick(selection, row) {
      this.selectData = selection;
    },
    //添加按钮
    insert() {
      const tableItem = new TableItem(this.getRelatedHook.guid);
      this.tableData.push(tableItem);
      this.tableData.forEach((item, index) => {
        item.index = index;
      });
    },
    //删除按钮
    remove() {
      const data = this.selectData;
      //判断是否为空
      if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        //arr存放传给后台删除id，arr1存放界面删除的索引
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.hook_Order_DetailId;
          arr1[index] = item.index;
        });
        //排序
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.tableData.splice(item, 1);
        });
        this.deleteData = arr;
      }
    },
    //删除操作
    delete() {
      DeleteHook(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    //校验
    validation() {
      try {
        this.tableData.forEach((item, index) => {
          for (var i in item) {
            if (item[i] === "") {
              this.$message.error("第" + (index + 1) + "行含有空字符串");
              this.isContainBlank = true;
              throw new Error("End Loop");
            } else {
              this.isContainBlank = false;
            }
          }
        });
      } catch (e) {
        //catch语句跳出forEach循环
      }
    },
    //提交操作
    submit() {
      //校验有没有空数据
     /*  this.validation(); */
      if (!this.isContainBlank) {
        this.delete();
        InsertOrUpdateHook(this.tableData).then((res) => {
          if (res.success) {
            this.$router.push("/hook");
          }
        });
      }
    },
    //取消
    cancel() {
      this.$router.go(-1);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
  },
};
</script>

<style scoped>
.el-row {
  margin-bottom: 10px;
}
.el-textarea__inner {
  height: 60px;
}
</style>
