import {request} from './request'

//Hook
//获得钩子采购单
export async function GetAllHooks(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllHooks',
        data:data
    })
}
//增加或更新钩子采购单
export async function InsertOrUpdateHook(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/InsertOrUpdateHook',
        data:data
    })
}
//获得相关ID采购单
export async function GetRelatedHooks(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetRelatedHooks',
        data:data
    })
}
//删除采购单
export async function DeleteHook(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteHook',
        data:data
    })
}
